.root {
  padding: .5rem 0;
}

.todoItem {
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  grid-gap: .5rem;
  box-shadow: 0 5px 7px -5px rgba(0, 0, 0, 0.75);
  animation: rootScale .2s;
}

.todoText {
  cursor: pointer;
  display: flex;
  align-items: center;
  word-break: break-word;

  svg {
    padding-right: .3rem;
  }
}

.edit > svg {
  padding-left: .3rem;
  cursor: pointer;
}

.checked {
  text-decoration: line-through solid red;
}

@keyframes rootScale {
  0% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}
